input[type="checkbox"] {
    /* visibility: hidden; */
    /* position:absolute;
    width: 25px;
    opacity: 0;
    margin-top: 13px; */
  }
input[type="checkbox"] + label:before {
    border: 1px solid #F88440;
    content: "\A0";
    display: inline-block;
    font: 16px/1em sans-serif;
    height: 16px;
    margin: 0 .25em 0 0;
    padding: 0;
    vertical-align: top;
    width: 16px;
    margin-top: 5px;
  }
  input[type="checkbox"]:checked + label:before {
    background: #F88440;
    color: #333;
    content: "\2713";
    text-align: center;
  }
  input[type="checkbox"]:checked + label:after {
    font-weight: bold;
  }
  
  input[type="checkbox"]:focus + label::before {
      outline: rgb(59, 153, 252) auto 5px;
  }
  
  .repeat-label {
    cursor: pointer;
    font-size: 16px;
    color: #2A2A2A;
    margin-top: 7px;
  }

